import {
	getDataKonsultan,
	postDataKonsultan,
	putDataKonsultan,
	deleteDataKonsultan
} from '@/config/konsultan'

var dataKonsultan = {
	namespaced: true,
	
	state: {
		dataKonsultan: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataKonsultan (state, payload) {
			state.dataKonsultan = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataKonsultan (context, payload) {
			getDataKonsultan(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataKonsultan', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataKonsultan (context, payload) {
			postDataKonsultan(payload)
				.then(res => {
					// console.log(res)
					if (res.status === 201) {
						context.dispatch('getDataKonsultan')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataKonsultan (context, payload) {
			putDataKonsultan(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataKonsultan')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})			
		},
		deleteDataKonsultan (context, payload) {
			deleteDataKonsultan(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataKonsultan')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataKonsultan
