import http from './http'

export function postDataKonsultan(data) {
	return http.post('konsultan', data)
}

export function putDataKonsultan(id, data) {
	return http.put('konsultan/'+id, data)
}

export function getDataKonsultan() {
	return http.get('konsultan')
}

export function deleteDataKonsultan(id) {
	return http.delete('konsultan/'+id)
}