import http from './http'

export function getDataPtnFilter(data) {
	return http.get(`master/ptn?kelompok=${data.kelompok}&nama=${data.ptn}&extended=1`)
}

export function getDataPtnByName() {
	return http.get(`master/ptn?extended=0`)
}

export function getDataPtn(data) {
	return http.get(`master/ptn?page=${data}&extended=1`)
}

export function getDataProdi(data) {
	return http.get(`master/ptn?nama=${data}&extended=1`)
}

export function getDetailPtn(data) {
	return http.get(`master/ptn/${data}?extended=1`)
}

export function deleteDataPtn(id) {
	return http.delete('ptn/'+id)
}