import http from './http'

export function getDataJurusan() {
	return http.get('master/jurusan')
}

export function getDataKurikulum() {
	return http.get('master/kurikulum')
}

export function getDataKelas() {
	return http.get('master/kelas')
}

export function getDataBidang() {
	return http.get('master/bidangprestasi')
}

export function getDataTingkat() {
	return http.get('master/tingkat')
}

export function getDataPelajaran() {
	return http.get('master/pelajaran')
}

export function getDataUnit(id) {
	return http.get('master/unit/'+id)
}

export function putDataUnit(id, data) {
	return http.patch('master/unit/'+id, data)
}

export function getDataJenisKelas() {
	return http.get('master/jeniskelas')
}

export function getDataJenjang() {
	return http.get('master/jenjang')
}

export function getDataSekolah(data) {
	return http.get(`sekolah?provinsi_id=${data.provinsi_id}&kabupaten_id=${data.kabupaten_id}`)
}