import {
	getDetailPtn,
	getDataPtn,
	getDataPtnFilter,
	getDataProdi,
	getDataPtnByName,
	deleteDataPtn
} from '@/config/ptn'

var dataPtn = {
	namespaced: true,
	
	state: {
		dataPtn: [],
		dataPtnByNama: [],
		dataProdi: [],
		detailPtn: [],
		status: null,
		isLoading: false,
		lengthPage: 0
	},

	mutations: {
		updateDataPtn (state, payload) {
			state.dataPtn = payload
		},
		updateDataPtnByNama (state, payload) {
			state.dataPtnByNama = payload
		},
		updateDataProdi (state, payload) {
			state.dataProdi = payload
		},
		updateLengthPage (state, payload) {
			state.lengthPage = payload
		},
		updateDetailPtn (state, payload) {
			state.detailPtn.push(payload)
		},
		emptyDetailPtn (state, payload) {
			state.detailPtn = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataPtn (context, payload) {
			getDataPtn(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataPtn', res.data.data)
						context.commit('updateLengthPage', res.data.meta.last_page)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataPtnFilter (context, payload) {
			getDataPtnFilter(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataPtn', res.data.data)
						context.commit('updateLengthPage', 0)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataPtnByName (context) {
			getDataPtnByName()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataPtnByNama', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataProdi (context, payload) {
			getDataProdi(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataProdi', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDetailPtn (context, payload) {
			context.commit('emptyDetailPtn', [])			
			payload.map((ptn) => {
				getDetailPtn(ptn.ptn_id)
					.then(res => {
						if (res.status === 200) {
							context.commit('updateDetailPtn', res.data.data)
							context.commit('updateStatus', true)
							context.dispatch('updateIsLoading', false)
						}
					})
					.catch(err => {
						context.commit('updateStatus', false)
						context.dispatch('updateIsLoading', false)
					})
			})
		},
		deleteDataPtn (context, payload) {
			deleteDataPtn(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataPtn')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataPtn
