import http from './http'

export function postDataPeserta(data) {
	return http.post('peserta', data)
}

export function getDataPeserta(data) {
	return http.get('peserta?page='+data)
}

export function getDataPesertaFilter(data) {
	return http.get(`peserta?
		provinsi_id=${data.provinsi}&
		kabupaten_id=${data.kabupaten}&
		jenjang_id=${data.jenjang}&
		sekolah_id=${data.sekolah}&
		kelas_id=${data.kelas}&
		prodi_id=${data.prodi}`)
}

export function deleteDataPeserta(id) {
	return http.delete('peserta/'+id)
}