import {
	postDataPeserta,
	getDataPeserta,
	getDataPesertaFilter,
	deleteDataPeserta
} from '@/config/peserta'

const dataPeserta = {
	namespaced: true,

	state: {
		dataPeserta: '',
		dataPersonal: '',
		dataSekolah: '',
		dataPrestasi: '',
		dataRapor: '',
		dataPilihanPtn: '',
		status: null,
		isLoading: false,
		lengthPage: 0
	},

	mutations: {
		updateDataPeserta(state, payload) {
			state.dataPeserta = payload
		},
		updateDataPersonal(state, payload) {
			state.dataPersonal = payload			
		},
		updateDataSekolah(state, payload) {
			state.dataSekolah = payload
		},
		updateDataPrestasi(state, payload) {
			state.dataPrestasi = payload
		},
		updateDataRapor(state, payload) {
			state.dataRapor = payload
		},
		updateDataPtn(state, payload) {
			state.dataPilihanPtn = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		},
		updateLengthPage(state, payload) {
			state.lengthPage = payload
		}
	},

	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},

		getDataPeserta(context, payload) {
			getDataPeserta(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataPeserta', res.data.data)
						context.commit('updateLengthPage', res.data.meta.last_page)
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},

		getDataPesertaFilter(context, payload) {
			getDataPesertaFilter(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataPeserta', res.data.data)
						context.commit('updateLengthPage', 0)
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},

		postDataPeserta(context, payload) {
			postDataPeserta(payload)
				.then(res => {
					if (res.status === 201) {
						context.dispatch('getDataPeserta')
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		},

		deleteDataPeserta(context, payload) {
			deleteDataPeserta(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataPeserta')
						context.dispatch('updateIsLoading', false)
						context.commit('updateStatus', true)
					}
				})
				.catch(err => {
					context.dispatch('updateIsLoading', false)
					context.commit('updateStatus', false)
				})
		}
	}
}

export default dataPeserta
