import {
	getDataJenisKelas,
	getDataTingkat,
	getDataKurikulum,
	getDataJenjang,
	getDataJurusan,
	getDataSekolah,
	getDataKelas,
	getDataBidang,
	getDataPelajaran,
	getDataUnit,
	putDataUnit
} from '@/config/admin'

var dataAdmin = {
	namespaced: true,
	
	state: {
		dataJenjang: [],
		dataKurikulum: [],
		dataTingkat: [],
		dataJenisKelas: [],
		dataSekolah: [],
		dataJurusan: [],
		dataPelajaran: [],
		dataBidang: [],
		dataKelas: [],
		dataUnit: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataKurikulum (state, payload) {
			state.dataKurikulum = payload
		},
		updateDataTingkat (state, payload) {
			state.dataTingkat = payload
		},
		updateDataJenjang (state, payload) {
			state.dataJenjang = payload
		},
		updateDataJenisKelas (state, payload) {
			state.dataJenisKelas = payload
		},
		updateDataSekolah (state, payload) {
			state.dataSekolah = payload
		},
		updateDataUnit (state, payload) {
			state.dataUnit = payload
		},
		updateDataKelas (state, payload) {
			state.dataKelas = payload
		},
		updateDataPelajaran (state, payload) {
			state.dataPelajaran = payload
		},
		updateDataBidang (state, payload) {
			state.dataBidang = payload
		},
		updateDataJurusan (state, payload) {
			state.dataJurusan = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataKurikulum (context, payload) {
			getDataKurikulum()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataKurikulum', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataTingkat (context, payload) {
			getDataTingkat()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataTingkat', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataJenjang (context, payload) {
			getDataJenjang(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataJenjang', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataJenisKelas (context, payload) {
			getDataJenisKelas(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataJenisKelas', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataSekolah (context, payload) {
			getDataSekolah(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataSekolah', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataUnit (context, payload) {
			getDataUnit(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataUnit', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataUnit (context, payload) {
			putDataUnit(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataUnit', payload.id)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataJurusan (context, payload) {
			getDataJurusan()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataJurusan', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataBidang (context, payload) {
			getDataBidang()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataBidang', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataPelajaran (context, payload) {
			getDataPelajaran()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataPelajaran', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataKelas (context, payload) {
			getDataKelas()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataKelas', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
	}
}

export default dataAdmin
