import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Home,
    meta: { requiresAuth: true },
    children: [
      {
        path: '/',
        name: 'dashboard',
        component: () => import('../views/Dashboard.vue')
      },
      {
        path: '/register',
        component: () => import('../views/Peserta.vue'),
        children: [
          {
            path: '/register',
            name: 'register',
            component: () => import('../components/Peserta/Register.vue')
          },
          {
            path: '/rekap',
            name: 'rekap',
            component: () => import('../components/Peserta/Rekap.vue')
          }
        ]
      },
      {
        path: '/ptn',
        component: () => import('../views/PTN.vue'),
        children: [
          {
            path: '/ptn',
            name: 'ptn',
            component: () => import('../components/PTN/DaftarPTN.vue')
          },
          {
            path: '/preview-ptn',
            name: 'preview-ptn',
            component: () => import('../components/PTN/PreviewPTN.vue')
          }
        ]
      },
      {
        path: '/peringkat',
        name: 'peringkat',
        component: () => import('../views/Peringkat.vue')
      },
      // {
      //   path: '/unit',
      //   name: 'unit',
      //   component: () => import('../views/Unit.vue')
      // },
      {
        path: '/konsultan',
        name: 'konsultan',
        component: () => import('../views/Konsultan.vue')
      },
      {
        path: '/akun',
        name: 'akun',
        component: () => import('../views/Akun.vue')
      },
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/Login.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some(route => route.meta.requiresAuth)
  const isLoggedIn = window.localStorage.getItem('token_unit')

  if (requiresAuth && !isLoggedIn){ 
    next({ name: 'login' })
  }else if(isLoggedIn && to.name === 'login') {
    next({ name: 'dashboard' })
  }else if (requiresAuth && isLoggedIn) {
    next()
  }else {
    next()
  }
})

export default router
